.dtree {
	/*font-family: Verdana, Geneva, Arial, Helvetica, sans-serif;*/
	font-size: 14px;
	color: #f48120;
	font-weight:bold;
	white-space: nowrap;
}


.dtree img { 
	border: 0px;
	vertical-align: middle;
	margin:0px;
}
.dtree a {
	font-size:14px;
	color: #333;
	text-decoration: none;
	font-weight:normal;
}
.dtree a img{}
.dtree a.node, .dtree a.nodeSel {
	/* white-space: nowrap; */
	/*padding: 1px 2px 1px 2px;*/
	margin-right: 24px;
}

.dtree a.node img{margin-top:-11px;}
.dtree a.node:hover, .dtree a.nodeSel:hover { 
	color: #d6680b;
	text-decoration:none;
}
.dtree sup{color:#2371c3; font-weight:normal;}

.dtree a.nodeSel {
	/*background-color: #c0d2ec;*/
	color: #f48120;
}
.dtree .clip {
	/*overflow: hidden; 0104  */		
	margin:0px; padding:0px;
}
.dTreeNode{height:23px;}
