.btn_down{position:relative; margin:7px 0px 24px 32px; }
/*.btn_down .view{cursor:pointer; }*/
.btn_down .hide{display:none; padding:15px 0; position:relative; background-color:#fff;}
.btn_down_list{width:200px; height:auto;  top:20px; left:-44px;  border:1px solid #a3a4a5;background:#fff; position:absolute; display:inline-block;  padding:16px; line-height:20px; box-shadow: 0 2px 0 0 rgba(0, 0, 0, 0.1), 0 0 0 0.5px rgba(0, 0, 0, 0.04); z-index:99; font-size:13px;}
.btn_down_list .user_name{width:90%; position:relative; text-align:center; line-height:30px; margin-right:10%;}
.btn_down_list .user_name .fa-user-circle{width:30px; font-size:30px;  }
.btn_down_list ul{ width:100%;}
.btn_down_list li {color:#8c8c8c; font-size:13px; line-height:30px; border-bottom:1px solid #eee;}
.btn_down_list li a			{width:100%; display:block;}
.btn_down_list li a:hover	{color:#f48120;}
.btn_down_list:after {   content: '';    position: absolute;    border-bottom: 13px solid #fff;    border-right: 8px solid transparent;    border-left: 8px solid transparent;    top: -11px;    left: 50%;	 margin-left: -5px; }
.btn_down_list:before {    content: '';    position: absolute;    border-bottom: 13px solid #a3a4a5;    border-right: 8px solid transparent;    border-left: 8px solid transparent;    top: -13px;    left:50%;	margin-left: -5px; }

